import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { AuthGuardService } from "src/app/services/auth/auth-guard.service";
import { HomeClientComponent } from "../home/home-connected/home-client.component";
import { LiveFreeComponent } from "../live/live-free/live-free.component";
import { LivePrivateComponent } from "../live/live-private/live-private.component";
import { LiveVipComponent } from "../live/live-vip/live-vip.component";
import { PaiementComponent } from "../paiement/paiement.component";
import { BuyCreditComponent } from "./buy-credit/buy-credit.component";
import { ConfirmEmailComponent } from "./confirm-email/confirm-email.component";
import { ProfilComponent } from "./profil/profil.component";
import { ReactivateAccountComponent } from "./reactivate-account/reactivate-account.component";
import { SigninComponent } from "./signin/signin.component";
import { SignupComponent } from "./signup/signup.component";


const routes: Routes = [
  {
    path: 'inscription',
    component: SignupComponent
  },
  {
    path: 'connexion',
    component: SigninComponent
  },
  {
    path: 'profile',
    component: ProfilComponent,
    canActivate: [AuthGuardService]
  },
  {
    path: 'achat_de_credit',
    component: BuyCreditComponent,
    canActivate: [AuthGuardService]
  },
  {
    path: 'paiement',
    component: PaiementComponent,
    canActivate: [AuthGuardService]
  },
  {
    path: 'accueil/registered',
    component: HomeClientComponent,
    canActivate: [AuthGuardService]
  },
  {
    path: 'live/free',
    component: LiveFreeComponent,
    canActivate: [AuthGuardService]
  },
  {
    path: 'live/private',
    component: LivePrivateComponent,
    canActivate: [AuthGuardService]
  },
  {
    path: 'live/vip',
    component: LiveVipComponent,
    canActivate: [AuthGuardService]
  },
  {
    path: 'register/confirm',
    component: ConfirmEmailComponent
  },
  {
    path: 'reactivation/account',
    component: ReactivateAccountComponent
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ClientRoutingModule { }
